<template>
    <v-app id="inspire" dark>
        <v-layout row>
            <v-card class="ml-9" style="width:20vw" :loading="telemetryLoading">
                <v-card-title class="justify-center">
                    Telemetry
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text v-html="telemetryText">
                </v-card-text>
            </v-card>
            <v-card id="fieldVcard" :loading="true" class="ml-6">
                <img src="Field2.png" style="height:100%" ref="FieldImage">
                <v-container style="padding: 0;">
                    <canvas id="fieldCanvas" ref="canvas" width="500" height="500"></canvas>
                </v-container>
            </v-card>
            <v-card>
                <v-layout column class="mx-12">
                    <v-list>
                        <v-list-item v-for="item in items"
                                     :key="item.title">
                            <v-list-item-content>
                                <v-btn @click="routeClicked(item.title)">
                                    <v-icon class="mx-3">{{ item.icon }}</v-icon>
                                    {{ item.title }}
                                </v-btn>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list>
                    <v-btn class="my-5" color="#007f80" @click="startOpmode()">Start Opmode</v-btn>
                    <v-btn color="#FF0000" @click="stopOpmode()">Stop Opmode</v-btn>
                </v-layout>
            </v-card>
        </v-layout>
        <v-dialog v-model="opmodeShown" width="500">
            <v-card>
                <v-card-title class="justify-center">
                    Opmodes
                </v-card-title>
                <v-divider></v-divider>
                <v-list dense>
                    <v-list-item v-for="item in opmodes" :key="item.title">
                        <v-btn width="470" @click="initOpmode(item.title)">
                            Init {{ item.title }}
                        </v-btn>
                    </v-list-item>
                </v-list>
            </v-card>
        </v-dialog>
        <v-dialog v-model="vFileShown" width="500">
            <v-card>
                <v-card-title class="justify-center">
                    Virtual SD
                </v-card-title>
                <v-divider class="mb-4"></v-divider>
                <v-card-subtitle class="text-center">
                    {{ currDir }}
                </v-card-subtitle>
                <v-divider></v-divider>
                <v-list dense class="text-left">
                    <v-btn width="470" color="#FF0000" @click="deleteDir()" class="ml-4" v-if="showDelete">
                        <v-icon>mdi-delete</v-icon>
                        Delete Folder
                    </v-btn>
                    <v-btn width="470" @click="fileClicked('', 'Back One')" class="ml-4" v-if="showBackOne">
                        <v-icon>mdi-keyboard-return</v-icon>
                        Back One
                    </v-btn>
                    <v-list-item v-for="item in vFiles" :key="item.title">
                        <v-btn width="470" @click="fileClicked(item.icon, item.title)">
                            <v-icon class="mx-3">
                                {{ item.icon }}
                            </v-icon>
                            {{ item.title }}
                        </v-btn>
                    </v-list-item>
                </v-list>
                <v-divider class="my-5"></v-divider>
                <v-btn color="#007f80" style="width:100%;" @click="vFileCreation = true"> New File </v-btn>
                <v-btn color="#007f80" style="width:100%;" class="my-3"> New Folder </v-btn>
            </v-card>
        </v-dialog>
        <v-dialog v-model="vFileEditor" fullscreen hide-overlay transition="dialog-bottom-transition">
            <v-card>
                <v-toolbar dark color="primary" :loading="fileTextLoading">
                    <v-btn icon dark @click="vFileEditor = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>Edit {{ currFile }}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-items>
                        <v-btn dark text @click="deleteFile()">
                            <v-icon class="mx-2">mdi-delete</v-icon>
                            Delete
                        </v-btn>
                        <v-btn dark text @click="saveFile()">
                            <v-icon class="mx-2">mdi-content-save</v-icon>
                            Save
                        </v-btn>
                    </v-toolbar-items>
                </v-toolbar>
                <v-progress-linear indeterminate
                                   color="cyan" :active="fileTextLoading"></v-progress-linear>
                <v-textarea solo flat v-model="fileText" auto-grow></v-textarea>
            </v-card>
        </v-dialog>
        <v-dialog v-model="vFileCreation" width="750">
            <v-card>
                <v-toolbar dark color="primary">
                    <v-toolbar-title>Create File</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-divider></v-divider>
                <v-text-field v-model="newFileName" label="File Name" class="mx-3"></v-text-field>
                <v-btn style="width:100%" @click="createFile()">Create!</v-btn>
            </v-card>
        </v-dialog>
        <v-dialog v-model="vDirCreation" width="750">
            <v-card>
                <v-toolbar dark color="primary">
                    <v-toolbar-title>Create File</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-divider></v-divider>
                <v-text-field v-model="newFileName" label="File Name" class="mx-3"></v-text-field>
                <v-btn style="width:100%" @click="createDir()">Create!</v-btn>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="gamepadConnected"
                    :timeout="snackbarTimeout">
            Gamepad Connected!
            <v-btn color="blue"
                    text
                    @click="gamepadConnected = false">
                Close
            </v-btn>
        </v-snackbar>
        <v-snackbar v-model="gamepadDisconnected"
                    :timeout="snackbarTimeout">
            Gamepad Disconnected!
            <v-btn color="blue"
                    text
                    @click="gamepadDisconnected = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-app>
</template>

<script>
    /* eslint-disable no-unused-vars */
    //        <canvas ref="canvas" style="height:100%"></canvas>
    //            <img src="Field1.png" style="height:100%">

var gamepads = {};

function gamepadHandler(event, connecting) {
    var gamepad = event.gamepad;
    // Note:
    // gamepad === navigator.getGamepads()[gamepad.index]

    if (connecting) {
        gamepads[gamepad.index] = gamepad;
        this.gamepadConnected = true;
    } else {
        delete gamepads[gamepad.index];
        this.gamepadDisconnected = true;
    }
}

  export default {
    props: {
      source: String,
    },
    data() {
      return {
          subText: "2020",
          drawer: false,
          currDir: "",
          currFile: "",
          fileText: "",
          newFileName: "",
          telemetryText: "",
          fileTextLoading: true,
          opmodeShown: false,
          vFileShown: false,
          vFileEditor: false,
          showDelete: false,
          vFileCreation: false,
          vDirCreation: false,
          gamepadConnected: false,
          gamepadDisconnected: false,
          showBackOne: false,
          positionStr: "70, 70, 45",
          snackbarTimeout: 2000,
          telemetryLoading: true,
          items: [
              { title: "Opmodes", icon: 'mdi-robot-industrial' },
              { title: "Virtual SD", icon: 'mdi-sd' },
          ],
          opmodes: [
              { title: "Opmodes Not Loaded" },
          ],
          vFiles: [

          ],
          vFileArray: [
              
          ],
      }
    },
    created () {
        this.$vuetify.theme.dark = true
        setInterval(() => {
            if (!this.drawer) {
                var ctx = this.$refs.canvas.getContext('2d');
                const robot = new Image();
                robot.src = "RobotW.png";
                robot.onload = () => {
                    ctx.canvas.width = this.$refs.FieldImage.width;
                    ctx.canvas.height = this.$refs.FieldImage.height;
                    ctx.webkitImageSmoothingEnabled = false;
                    ctx.mozImageSmoothingEnabled = false;
                    ctx.imageSmoothingEnabled = false;

                    var sizeWidth = ctx.canvas.width;
                    var sizeHeight = ctx.canvas.height;

                    var scaleWidth = sizeWidth / 141;
                    var scaleHeight = sizeHeight / 141;

                    ctx.save();
                    ctx.translate((parseFloat(this.positionStr.split(",")[0]) * scaleWidth), (parseFloat(this.positionStr.split(",")[1]) * scaleHeight));
                    ctx.rotate(parseFloat(this.positionStr.split(",")[2]) * Math.PI / 180);
                    ctx.drawImage(robot, -(scaleWidth * 9), -(scaleHeight * 9), scaleWidth * 18, scaleHeight * 18);
                };
            }
            this.refreshPosition();
        }, 50);
        setInterval(() => {
            var i;
            for (i in gamepads) {
                var gamepad = gamepads[i];
                var j;
                var s = "";
                for (j in gamepad.buttons) {
                    s += gamepad.buttons[j].value + ",";
                }
                var k;
                for (k in gamepad.axes) {
                    if (Math.abs(gamepad.axes[k]) > 0.05) {
                        s += gamepad.axes[k] + ",";
                    } else {
                        s += 0 + ",";
                    }
                }
            }
            this.sendData(s);
            this.refreshTelemetry();
        }, 200);
        var funcRan = false;
        setInterval(() => {
            if (!funcRan) {
                var canvas = this.$refs.canvas;
                canvas.addEventListener('click', function (e) {
                    var rect = canvas.getBoundingClientRect();
                    var ctx = canvas.getContext('2d');
                    var sizeWidth = ctx.canvas.width;
                    var sizeHeight = ctx.canvas.height;

                    var scaleWidth = sizeWidth / 141;
                    var scaleHeight = sizeHeight / 141;
                    var pos = {
                        x: (e.clientX - rect.left) / scaleWidth,
                        y: (e.clientY - rect.top) / scaleHeight
                    };
                    this.sendCoords(pos.x + ", " + pos.y);
                }.bind(this), false);
                window.addEventListener("gamepadconnected", function (e) {
                    var gamepad = e.gamepad;
                    gamepads[gamepad.index] = gamepad;
                    this.gamepadConnected = true;
                }.bind(this), false);
                window.addEventListener("gamepaddisconnected", function (e) {
                    var gamepad = e.gamepad;
                    delete gamepads[gamepad.index];
                    this.gamepadDisconnected = true;
                }.bind(this), false);
                funcRan = true;
            }
        }, 2000);
    },
    methods: {
        routeClicked(info) {
            if (info === "Opmodes") {
                this.getOpmodes();
                this.opmodeShown = true;
            }
            if (info === "Virtual SD") {
                this.currDir = "";
                this.currFile = "";
                this.getVfiles();
                this.getVfolders();
            }
        },
        fileClicked(info, folder) {
            if (folder === "Return To Start") {
                this.currDir = "";
                this.setFilesAndFolders();
            }
            if (folder === "Back One") {
                var corn = this.currDir.split("/");
                corn.pop();
                this.currDir = corn.join("/");
                this.setFilesAndFolders();
            }
            if (info === "mdi-folder") {
                if (this.currDir === "") {
                    this.currDir = folder;
                } else {
                    this.currDir = this.currDir + "/" + folder;
                }
                this.setFilesAndFolders();
            }
            if (info === "mdi-file") {
                this.currFile = folder;
                this.fileTextLoading = true;
                this.vFileEditor = true;
                this.getVFile(this.currDir + "/" + folder);
            }
            if (this.currDir === "") {
                this.showDelete = false;
                this.showBackOne = false;
            } else {
                var safeToDelete = true;
                var vFile;
                for (vFile in this.vFileArray) {
                    if (this.vFileArray[vFile].folder === this.currDir) {
                        safeToDelete = false;
                    }
                }
                this.showDelete = safeToDelete;
                this.showBackOne = true;
            }
        },
        getVFile(file) {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/" + file + ".vfile", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    text = text.replace(/\+/g, '%20');
                    this.fileText = decodeURIComponent(text);
                    this.fileTextLoading = false;
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null); 
        },
        getOpmodes() {
            while (this.opmodes.length > 0) { this.opmodes.pop() }
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/opmodes.txt", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    var arr = text.split("&");
                    var i;
                    for (i in arr) {
                        this.opmodes.push({ title: arr[i] });
                    }
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null); 
        },
        getVfiles() {
            while (this.vFileArray.length > 0) { this.vFileArray.pop() }
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/filesindex.txt", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    var arr = text.split("&");
                    var i;
                    for (i in arr) {
                        var titleArr = arr[i].split("/");
                        if (titleArr.length == 1) {
                            this.vFileArray.push({ title: arr[i], icon: "mdi-file", folder: "" });
                        } else {
                            var j = arr[i].lastIndexOf("/");
                            this.vFileArray.push({ title: arr[i].slice(j+1), icon: "mdi-file", folder: arr[i].slice(0, j) });
                        }
                    }
                    this.vFileShown = true;
                    var vFile;
                    while (this.vFiles.length > 0) { this.vFiles.pop() }
                    this.vFiles.push({ title: "Return To Start", icon: 'mdi-keyboard-return', folder: "" });
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null);
        },
        getVfolders() {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/foldersindex.txt", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    var arr = text.split("&");
                    var i;
                    for (i in arr) {
                        var titleArr = arr[i].split("/");
                        if (titleArr.length == 1) {
                            this.vFileArray.push({ title: arr[i], icon: "mdi-folder", folder: "" });
                        } else {
                            var j = arr[i].lastIndexOf("/");
                            this.vFileArray.push({ title: arr[i].slice(j + 1), icon: "mdi-folder", folder: arr[i].slice(0, j) });
                        }
                    }
                    this.vFileShown = true;
                    var vFile;
                    for (vFile in this.vFileArray) {
                        if (this.vFileArray[vFile].folder === this.currDir) {
                            this.vFiles.push(this.vFileArray[vFile]);
                        }
                    }
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null);
        },
        setFilesAndFolders() {
            var vFile;
            while (this.vFiles.length > 0) { this.vFiles.pop() }
            this.vFiles.push({ title: "Return To Start", icon: 'mdi-keyboard-return', folder: "" });
            for (vFile in this.vFileArray) {
                if (this.vFileArray[vFile].folder === this.currDir) {
                    this.vFiles.push(this.vFileArray[vFile]);
                }
            }
        },
        saveFile() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/" + (this.currDir + "/" + this.currFile) + ".vfile", true);
            console.log(this.fileText);
            xhr.send(encodeURIComponent(this.fileText + "ENDTRANSMISSION"));
            this.vFileEditor = false;
        },
        createDir() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/" + (this.currDir + "/" + this.newFileName) + ".vfolder", true);
            this.newFileName = "";
            this.vFileArray.push({ title: this.newFileName, icon: 'mdi-file', folder: this.currDir });
            this.setFilesAndFolders();
            xhr.send(" " + "ENDTRANSMISSION");
        },
        deleteDir() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/" + (this.currDir) + ".vdeletefolder", true);
            this.newFileName = "";
            this.currDir = "";
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    this.getVfiles();
                    this.getVfolders();
                }
            }.bind(this);
            this.showDelete = false;
            this.showBackOne = false;
            xhr.send(" " + "ENDTRANSMISSION");
        },
        createFile() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/" + (this.currDir + "/" + this.newFileName) + ".vfile", true);
            this.vFileCreation = false;
            this.vFileArray.push({ title: this.newFileName, icon: 'mdi-file', folder: this.currDir });
            this.newFileName = "";
            this.setFilesAndFolders();
            xhr.send(" " + "ENDTRANSMISSION");
        },
        deleteFile() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/" + (this.currDir + "/" + this.currFile) + ".vdeletefile", true);
            this.newFileName = "";
            this.vFileCreation = false;
            this.vFileEditor = false;
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    this.getVfiles();
                    this.getVfolders();
                }
            }.bind(this);
            xhr.send(" " + "ENDTRANSMISSION");
        },
        initOpmode(name) {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/upload.txt", true);
            xhr.send("console=init " + name + "ENDTRANSMISSION");
            this.opmodeShown = false;
        },
        startOpmode() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/upload.txt", true);
            xhr.send("console=start" + "ENDTRANSMISSION");
        },
        stopOpmode() {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/upload.txt", true);
            xhr.send("console=stop" + "ENDTRANSMISSION");
        },
        sendData(gamepad) {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/upload", true);
            var toSend = "";
            toSend += "gamepad=" + gamepad + "&";
            xhr.send(toSend + "ENDTRANSMISSION");
        },
        sendCoords(coords) {
            var xhr = new XMLHttpRequest();
            xhr.open("POST", "http://192.168.1.245:4750/upload", true);
            var toSend = "";
            toSend += "coords=" + coords + "&";
            xhr.send(toSend + "ENDTRANSMISSION");
        },
        refreshTelemetry() {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/telemetry.txt", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    text = text.replace(/\+/g, '%20');
                    this.telemetryText = decodeURIComponent(text.replace(/&/g, "<br/>"));
                    if (text === "") {
                        this.telemetryLoading = true;
                    } else {
                        this.telemetryLoading = false;
                    }
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null); 
        },
        refreshPosition() {
            var xhr = new XMLHttpRequest();
            xhr.open("GET", "http://192.168.1.245:4750/position.txt", true);
            xhr.onload = function (e) {
                if (xhr.readyState === 4) {
                    var text = xhr.responseText;
                    text = text.replace(/\+/g, '%20');
                    this.positionStr = decodeURIComponent(text);
                }
            }.bind(this);
            xhr.onerror = function (e) {
                console.error(xhr.statusText);
            };
            xhr.send(null); 
        }
    }
  }
/* eslint-enable no-unused-vars */
</script>
<style>
    img {
        padding: 0;
        display: block;
        margin: 0 auto;
        max-height: 100vh;
        height: auto;
        max-width: 100vh;
        z-index: 1;
        position:absolute;
    }
    #fieldVcard {
        padding: 0;
        display: block;
        margin: 0 auto;
        max-height: 100vh;
        height: auto;
        max-width: 100vh;
        width:100vh;
    }
    #fieldCanvas {
        padding: 0;
        z-index: 20;
        position: relative;
        image-rendering: optimizeSpeed;
        image-rendering: -moz-crisp-edges;
        image-rendering: -webkit-optimize-contrast;
        image-rendering: -o-crisp-edges;
        image-rendering: crisp-edges;
        -ms-interpolation-mode: nearest-neighbor;
    }
</style>
