import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
/* eslint-disable no-unused-vars */
import VueRouter from 'vue-router'
Vue.use(VueRouter)
Vue.config.productionTip = false

var app = new Vue({
  vuetify,
  render: h => h(App),
}).$mount('#app')
/* eslint-enable no-unused-vars */